.service {
	&__title {
		margin-bottom: 78px;
		@include md {
			padding: 0 20px; }
		@include sm {
			margin-bottom: 18px;
			max-width: 330px; } }
	&__container {
		display: table;
		width: 100%;
		color: $white;
		@include lg {
			display: block; }
		@include md {
			display: table; }
		@include sm {
			display: block; } }
	&__row {
		display: table-row;
		@include lg {
			display: block; }
		@include md {
			display: table-row; }
		@include sm {
			display: block; } }
	&__col {
		display: table-cell;
		vertical-align: top;
		padding: 96px 50px 101px;
		background: #2D3353;
		@include lg {
			display: block; }
		@include md {
			display: table-cell;
			padding: 70px 20px; }
		@include sm {
			display: block; }
		&:last-child {
			background-image: linear-gradient(151deg, #2D3353 8%, #3E3E88 100%); } }
	&__icon {
		margin-bottom: 30px;
		height: 46px;
		@include sm {
			margin-bottom: 30px;
			height: auto; }
		.icon {
			display: inline-block;
			vertical-align: top;
			fill: $white;
			&-diamond {
				font-size: 37px; }
			&-chat {
				font-size: 42px; } } }
	&__subtitle {
		margin-bottom: 27px;
		@include sm {
			margin-bottom: 19px; } }
	&__description {
		max-width: 380px;
		@include f(18px, 400, 28px);
		@include lg {
			max-width: none; }
		@include sm {
			@include f(13px, null, 18px); } } }
