body {
  color: #333;
  font-family: "GraphikLCG", "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.out {
  position: relative;
  overflow: hidden;
}

.inner {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1600px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.main {
  padding: 133px 0 0 40px;
  margin: 0 auto;
  max-width: 1680px;
}

@media only screen and (max-width: 1260px) {
  .main {
    padding: 100px 0 0 20px;
  }
}

@media only screen and (max-width: 1023px) {
  .main {
    padding: 36px 0 0;
  }
}

.main__inner {
  position: relative;
  padding: 21px 0 0 380px;
}

@media only screen and (max-width: 1260px) {
  .main__inner {
    padding: 0 0 0 340px;
  }
}

@media only screen and (max-width: 1023px) {
  .main__inner {
    padding-left: 0;
  }
}

.title-xl {
  font-size: 120px;
  font-weight: 700;
  line-height: 1.084;
  letter-spacing: -4px;
}

@media only screen and (max-width: 767px) {
