    font-size: 60px;
    line-height: 1.216;
    letter-spacing: -2px;
  }
}

.title-lg {
  font-size: 80px;
  font-weight: 700;
  line-height: 1.14;
  letter-spacing: -1px;
}

.title-lg_simple {
  line-height: 1;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 40px;
    line-height: 1.125;
  }
}

.title-md {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.1875;
}

@media only screen and (max-width: 767px) {
  .title-md {
    font-size: 28px;
    line-height: 1.214;
  }
}

.label {
  color: #626262;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.article {
  color: #333;
}

.article .title-xl {
  padding-bottom: 60px;
}

@media only screen and (max-width: 767px) {
  .article .title-xl {
    padding-bottom: 28px;
  }
}

.article .title-md {
  padding-top: 60px;
  padding-bottom: 20px;
}

@media only screen and (max-width: 767px) {
  .article .title-md {
    padding-top: 26px;
    padding-bottom: 20px;
  }
}

.article p,
.article li {
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.555;
}

@media only screen and (max-width: 767px) {
  .article p,
  .article li {
    padding-top: 14px;
    padding-bottom: 14px;
  }
}

.article ol {
  list-style-type: decimal;
  list-style-position: outside;
  padding-left: 20px;
}

.article ol li {
  padding-left: 10px;
}

.article ul li {
  position: relative;
  padding-left: 30px;
}

@media only screen and (max-width: 767px) {
  .article ul li {
    padding-left: 40px;
  }
}

.article ul li:before {
  content: '';
  position: absolute;
  top: 29px;
  left: 0;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: #333;
}

@media only screen and (max-width: 767px) {
  .article ul li:before {
    top: 23px;
    left: 7px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 40px;
  color: #fff;
  z-index: 100;
}

.header__inner {
  max-width: 1680px;
}

.logo {
  float: left;
}

.nav {
  float: left;
  margin-left: 167px;
  font-size: 16px;
  font-weight: 700;
}

.nav__item {
  vertical-align: top;
  display: inline-block;
}

.nav__item:not(:first-child) {
  margin-left: 63px;
}

.nav__item.is-active .nav__link {
  opacity: 1;
}

.nav__link {
  opacity: .6;
  color: #fff;
  transition: opacity .3s ease-in-out;
}

.no-touch .nav__link:hover {
  opacity: 1;
}

.lang {
  position: relative;
  float: right;
  padding-right: 12px;
  min-width: 12px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  user-select: none;
}

.lang:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  margin-top: -2px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 3.5px 0 3.5px;
  border-color: #fff transparent transparent transparent;
}

.lang__list {
  position: absolute;
  top: 100%;
  width: 42px;
  left: 0;
  display: none;
}

.lang__item {
  display: block;
}

.topic {
  position: relative;
  padding: 185px 0 150px;
  height: 100vh;
  color: #fff;
  text-align: center;
  font-size: 0;
  background-image: linear-gradient(138deg, #2D3353 9%, #3E3E88 70%);
}

.topic:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.topic__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-position: 40% 21%;
  background-repeat: no-repeat;
}

.topic__left {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  padding: 260px 0 44px 33px;
  font-size: 12px;
}

.topic__mail {
  position: absolute;
  left: 73px;
  bottom: 33px;
  color: #fff;
  font-size: 12px;
  font-weight: 400;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.topic__inner {
  position: relative;
  padding: 0 180px 0 419px;
  z-index: 1;
  width: 100%;
  max-width: 1680px;
  text-align: left;
  vertical-align: middle;
  display: inline-block;
}

.topic__footer {
  margin-top: 35px;
}

.share {
  font-size: 15px;
}

.share__item {
  display: block;
}

.share__item:not(:last-child) {
  margin-bottom: 5px;
}

.share a {
  color: inherit;
}

.privacy {
  position: relative;
  padding: 204px 60px 100px 20px;
  margin: 0 auto;
  max-width: 880px;
}

.privacy:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 767px) {
  .privacy {
    padding: 118px 20px 20px;
  }
}

.contact {
  padding-top: 83px;
  height: 100vh;
  color: #000;
}

@media only screen and (max-width: 767px) {
  .contact {
    padding-top: 73px;
    padding-bottom: 37px;
    height: auto;
  }
}

.contact__map {
  position: relative;
  overflow: hidden;
  margin-left: 420px;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .contact__map {
    height: 316px;
    margin-bottom: -63px;
  }
}

.contact__map-inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.contact__title, .contact__mail {
  white-space: nowrap;
}

.contact__title {
  position: absolute;
  z-index: 2;
  top: 185px;
  left: 40px;
  padding: 7px 0;
}

@media only screen and (max-width: 767px) {
  .contact__title {
    position: relative;
    top: auto;
    left: auto;
    padding: 36px 18px 31px;
  }
}

.contact__mail {
  display: block;
  margin-top: 40px;
  color: #0171BB;
  font-size: 32px;
  font-weight: 700;
  line-height: 36px;
}

@media only screen and (max-width: 767px) {
  .contact__mail {
    margin-top: 16px;
    font-size: 20px;
    line-height: 36px;
  }
}

.contact__address {
