  }
}

.contact__info {
  position: absolute;
  z-index: 2;
  left: 40px;
  bottom: 127px;
}

@media only screen and (max-width: 767px) {
  .contact__info {
    position: relative;
    left: auto;
    bottom: auto;
    padding: 20px 20px 22px;
