    left: auto;
    padding: 0 17px;
    font-size: 12px;
    line-height: 14px;
  }
}

.contact__nav {
  right: 40px;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .contact__nav {
    display: none;
  }
}

.contact__nav a {
  display: inline-block;
  vertical-align: top;
  padding-top: 3px;
  color: #000;
  font-size: 12px;
  line-height: 14px;
}

.contact__nav a:not(:last-child) {
  margin-right: 16px;
}

.contact__copy, .contact__nav {
  position: absolute;
  z-index: 2;
  bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .contact__copy, .contact__nav {
    position: relative;
    bottom: auto;
  }
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 17px 48px;
  border-radius: 4px;
  color: #000;
  background: #fff;
  font-family: "GraphikLCG", "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  text-decoration: none;
}

@media only screen and (max-width: 767px) {
