    line-height: 17px;
  }
}

.btn .icon-arr-bottom {
  margin-right: 14px;
  font-size: 9px;
  vertical-align: middle;
}

.sticky-nav {
  position: absolute;
  top: 0;
  left: 0;
  padding: 36px 39px 43px;
  width: 305px;
  background: #F7F7F7;
}

@media only screen and (max-width: 1023px) {
  .sticky-nav {
    display: none;
  }
}

.sticky-nav__label {
  margin-bottom: 22px;
}

.sticky-nav__link {
  color: #D4D4D4;
  font-size: 28px;
  font-weight: 700;
  line-height: 2;
  cursor: pointer;
  transition: color 0.25s;
}

.sticky-nav__link.is-active {
  color: #3E3E89;
}

.no-touch .sticky-nav__link:hover {
  color: #3E3E89;
}

.sticky-nav__line {
  position: absolute;
  top: 0;
  right: 0;
  border-right: 5px solid #3E3E89;
  height: 26px;
  transform: translate3d(0, 0, 0);
}

.section {
  position: relative;
  padding-bottom: 192px;
}

@media only screen and (max-width: 1260px) {
  .section {
    padding: 50px 0;
  }
}

@media only screen and (max-width: 1023px) {
  .section {
    padding: 21px 0 20px;
  }
}

.section:first-child {
  padding-top: 15px;
}

.section:last-child {
  padding-bottom: 78px;
}

@media only screen and (max-width: 1260px) {
  .section:first-child, .section:last-child {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media only screen and (max-width: 767px) {
