.sticky-nav {
	position: absolute;
	top: 0;
	left: 0;
	padding: 36px 39px 43px;
	width: 305px;
	background: $lightGray;
	@include md {
		display: none; }
	&__label {
		margin-bottom: 22px; }
	&__link {
		color: #D4D4D4;
		@include f(28px, 700, 2);
		cursor: pointer;
		transition: color 0.25s;
		&.is-active {
			color: $violet; }
		@include hover {
			color: $violet; } }
	&__line {
		position: absolute;
		top: 0;
		right: 0;
		border-right: 5px solid $violet;
		height: 26px;
		@include gpu; } }
