.client {
	padding-top: 254px;
	@include lg {
		padding-top: 130px; }
	@include sm {
		padding-top: 86px; }
	&__title {
		margin-bottom: 78px;
		@include sm {
			padding: 0 20px;
			margin-bottom: 34px; } }
	&__container {
		position: relative;
		display: block;
		height: 778px;
		@include vertical;
		@include lg {
			height: 500px; }
		@include sm {
			height: 324px; } }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-image: linear-gradient(151deg, #0A2F2A 8%, #2D5442 100%); }
	&__logo {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle;
		.icon {
			fill: $white;
			font-size: 203px;
			@include sm {
				font-size: 96px; } } } }
