.contact {
	padding-top: 83px;
	height: 100vh;
	color: $black;
	@include sm {
		padding-top: 73px;
		padding-bottom: 37px;
		height: auto; }
	&__map {
		position: relative;
		overflow: hidden;
		margin-left: 420px;
		height: 100%;
		@include sm {
			height: 316px;
			margin-bottom: -63px; }
		&-inner {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%; } }
	&__title,
	&__mail {
		white-space: nowrap; }
	&__title {
		position: absolute;
		z-index: 2;
		top: 185px;
		left: 40px;
		padding: 7px 0;
		@include sm {
			position: relative;
			top: auto;
			left: auto;
			padding: 36px 18px 31px; } }
	&__mail {
		display: block;
		margin-top: 40px;
		color: #0171BB;
		@include f(32px, 700, 36px);
		@include sm {
			margin-top: 16px;
			@include f(20px, null, 36px); } }
	&__address {
		@include f(24px, null, 28px);
		@include sm {
			font-size: 20px; } }
	&__info {
		position: absolute;
		z-index: 2;
		left: 40px;
		bottom: 127px;
		@include sm {
			position: relative;
			left: auto;
			bottom: auto;
			padding: 20px 20px 22px;
			background: rgba($white, 0.7); } }
	&__copy {
		left: 40px;
		@include f(14px, 700, 17px);
		@include sm {
			left: auto;
			padding: 0 17px;
			@include f(12px, null, 14px); } }
	&__nav {
		right: 40px;
		font-size: 0;
		@include sm {
			display: none; }
		a {
			display: inline-block;
			vertical-align: top;
			padding-top: 3px;
			color: $black;
			@include f(12px, null, 14px);
			&:not(:last-child) {
				margin-right: 16px; } } }
	&__copy,
	&__nav {
		position: absolute;
		z-index: 2;
		bottom: 30px;
		@include sm {
			position: relative;
			bottom: auto; } } }
