.title-xl {
	@include f(120px, 700, 1.084);
	letter-spacing: -4px;
	@include sm {
		@include f(60px, null, 1.216);
		letter-spacing: -2px; } }

.title-lg {
	@include f(80px, 700, 1.14);
	letter-spacing: -1px;
	&_simple {
		line-height: 1; }
	@include sm {
		@include f(40px, null, 1.125); } }

.title-md {
	@include f(32px, 700, 1.1875);
	@include sm {
		@include f(28px, null, 1.214); } }

.label {
	color: #626262;
	@include f(16px, 400, 1.5); }

.article {
	color: $dark;
	.title-xl {
		padding-bottom: 60px;
		@include sm {
			padding-bottom: 28px; } }
	.title-md {
		padding-top: 60px;
		padding-bottom: 20px;
		@include sm {
			padding-top: 26px;
			padding-bottom: 20px; } }
	p,
	li {
		padding-top: 20px;
		padding-bottom: 20px;
		@include f(18px, 400, 1.555);
		@include sm {
			padding-top: 14px;
			padding-bottom: 14px; } }
	ol {
		list-style-type: decimal;
		list-style-position: outside;
		padding-left: 20px;
		li {
			padding-left: 10px; } }
	ul {
		li {
			position: relative;
			padding-left: 30px;
			@include sm {
				padding-left: 40px; }
			&:before {
				content: '';
				position: absolute;
				top: 29px;
				left: 0;
				border-radius: 50%;
				width: 8px;
				height: 8px;
				background: $dark;
				@include sm {
					top: 23px;
					left: 7px; } } } } }
